* {
  box-sizing: border-box;
}

.App {
  width: 100%;
  height: 100%;
  color: white;

  display: flex;
  flex-direction: column;
}

.List {
  flex-grow: 1;
  width: 100%;
  overflow-y: scroll;
  background-color: black;
}

.Padder {
  width: 100%;
  height: 70%;
}

.ListItem {
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: .25rem;
  padding-bottom: .25rem;
  display: flex;
  justify-content: space-between;
}

.ListItem:nth-child(odd) {
  background-color: #222;
}

.Selector {
  height: 3rem;
  width: 100%;
  flex-shrink: 0;
  border-top: 1px white solid;
  background-color: black;

  display: flex;
}

.Selector > button {
  flex-grow: 1;
}

.Selector > button:not(:last-child) {
  border-right: 1px white solid;
}

.Selector > button > img {
  filter: invert(100%);
  height: 50%;
  width: 50%;
}

.SearchBar {
  border-top: 1px white solid;
  flex-shrink: 0;
  background-color: black;
  width: 100%;
  height: 2rem;
  display: flex;
}

.SearchBar > input {
  flex-grow: 1;
  margin: 0;
  border: none;
  background-color: black;
  color: white;
  outline: none;
  padding-left: 1rem;
  padding-right: 1rem;
}

.SearchBar > button {
  height: 100%;
  border-left: 1px white solid;
}

.Question {
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 75vw;
  overflow: hidden;
}

.Answer {
  margin-left: 1.5rem;
  max-width: 33%;
  flex-shrink: 0;
}